import { apply, eq, number, option, ord, readonlyArray as RA, readonlyNonEmptyArray as RNEA, readonlyRecord, readonlySet as RS, readonlyTuple as RT, refinement, semigroup, string } from 'fp-ts';
import { flow, pipe } from 'fp-ts/lib/function';
import { ordAscending } from '../lib';
import { Card, Deck, eqCard, Hand, ordCardDescending, Suit, suits } from './deck';


export const directions = ['N', 'E', 'S', 'W'] as const
export type Direction = typeof directions[number]
export const eqDirection : eq.Eq<Direction> = string.Eq
export const ordDirection = ordAscending(directions)

export const partnerships = ["NorthSouth", "EastWest"] as const
export type Partnership = typeof partnerships[number]
export const eqPartnership : eq.Eq<Partnership> = string.Eq
export const ordPartnership = ordAscending(partnerships)

export const getPartnershipByDirection = (d: Direction): Partnership =>
  (d === 'N' || d === 'S') ? "NorthSouth" : "EastWest"

export type Deal = readonlyRecord.ReadonlyRecord<Direction, Hand>
export type Player = {
  direction: Direction
  hand: Hand
}
export const eqHand : eq.Eq<Hand> = RS.getEq(eqCard)

export const deal = (deck: Deck) : Deal =>
  pipe(directions,
    RNEA.zip(RNEA.chunksOf(13)(deck)),
    RNEA.groupBy(RT.fst),
    readonlyRecord.map(flow(RNEA.head, RT.snd, RS.fromReadonlyArray(eqCard))),
    (x: readonlyRecord.ReadonlyRecord<Direction, Hand>) => x)
export const eqDeal : eq.Eq<Deal> =
  readonlyRecord.getEq<Direction, Hand>(eqHand)

export const vulnerabilities = ["Neither", "NorthSouth", "EastWest", "Both"] as const
export type Vulnerability = typeof vulnerabilities[number]

export const getIsVulnerable = (dir: Direction, vul: Vulnerability) =>  
  !(vul === "Neither") && ((vul === "Both") || getPartnershipByDirection(dir) === vul)

export const strains = [...suits, 'N'] as const
export type Strain = typeof strains[number]
export const eqStrain : eq.Eq<Strain> = eq.eqStrict
export const ordStrain : ord.Ord<Strain> = ordAscending(strains)

export const minors: ReadonlyArray<Strain> = ['C', 'D']
export const majors: ReadonlyArray<Strain> = ['H', 'S']

export interface Board {
  dealer: Direction
  deal: Deal
}
export const eqBoard : eq.Eq<Board> = eq.struct({
  dealer: eqDirection,
  deal: eqDeal
})

export interface BoardWithDetail extends Board {
  number: number
  vulnerability: Vulnerability
}

const boneChart = (boardNumber: number) : Vulnerability => {
  switch ((boardNumber % 16) + 1) {
    case 1: case 8: case 11: case 14: return "Neither"
    case 2: case 5: case 12: case 15: return "NorthSouth"
    case 3: case 6: case 9:  case 16: return "EastWest"
    case 4: case 7: case 10: case 13: return "Both"
    default: throw Error("Not possible")
  }
}

export const makeBoard = (number: number) => (deal: Deal) : BoardWithDetail => ({
  number,
  dealer: directions[(number - 1) % directions.length],
  deal,
  vulnerability: boneChart(number)
})

export type NonContractBid = "Pass" | "Double" | "Redouble"
export const eqNonContractBid : eq.Eq<NonContractBid> = string.Eq
export interface ContractBid {
  level: number
  strain: Strain
}
export const eqContractBid : eq.Eq<ContractBid> = eq.struct({
  level: number.Eq,
  strain: string.Eq
})
export const ordContractBid : ord.Ord<ContractBid> =
  ord.getMonoid<ContractBid>().concat(
    pipe(number.Ord, ord.contramap(c => c.level)),
    pipe(ordStrain, ord.contramap(c => c.strain))
  )
export const contractBids : ReadonlyArray<ContractBid> =
  pipe(
    apply.sequenceS(RA.Apply)(({
      level: RA.makeBy(7, level => level + 1),
      strain: strains
    })),
    RA.sort(ordContractBid))

export type Bid = NonContractBid | ContractBid
export const isNonContractBid = (b: Bid) : b is NonContractBid => typeof b === "string"

export const eqBid : eq.Eq<Bid> = eq.fromEquals((x, y) =>
  (isNonContractBid(x) && isNonContractBid(y) && eqNonContractBid.equals(x, y)) ||
  (!isNonContractBid(x) && !isNonContractBid(y) && eqContractBid.equals(x, y)))

export const contractModifiers = ["Undoubled", "Doubled", "Redoubled"] as const
export type ContractModifier = typeof contractModifiers[number]
export interface Contract extends ContractBid {
  modifier: ContractModifier
}
export const eqContract : eq.Eq<Contract> = eq.struct({
  level: number.Eq,
  strain: string.Eq,
  modifier: string.Eq
})
export const fromBid = (bid: ContractBid): Contract =>
  ({ ...bid, modifier: "Undoubled" })

export type Auction = RNEA.ReadonlyNonEmptyArray<Bid>
const consecutivePasses = ["Pass", "Pass", "Pass"] as const
export type NonPassAuction = Auction & [...Auction, ...typeof consecutivePasses]
const passout = ["Pass", "Pass", "Pass", "Pass"] as const
export type PassAuction = typeof passout
export type CompletedAuction = NonPassAuction | PassAuction
export const eqAuction : eq.Eq<Auction> = RNEA.getEq(eqBid)
export const isCompletedAuction : refinement.Refinement<Auction, CompletedAuction> = (a): a is CompletedAuction =>
  a.length >= 4 &&
  (eqAuction.equals(a, passout) || eqAuction.equals(a.slice(a.length - 3) as unknown as Auction, consecutivePasses))

export interface BoardWithAuction extends Board {
  auction: Auction 
}
export interface BoardWithCompletedAuction extends BoardWithAuction {
  auction: CompletedAuction
}

export const getCardHcp = (card: Card) =>
  Math.max(0, card.rank - 10)

export const getHcp =
  flow(
    RS.toReadonlyArray(ordCardDescending),
    RA.foldMap(number.MonoidSum)(getCardHcp))

export type Shape = readonly [number, number, number, number]
export const zeroShape: Shape = [0, 0, 0, 0]
export const sortShape = (s: Shape) => pipe(s, RA.sort(ord.reverse(number.Ord))) as Shape
export const makeShape = (...counts: Shape) =>
  pipe(counts, sortShape)
export const eqShape : eq.Eq<Shape> =
  eq.contramap(sortShape)(RA.getEq(number.Eq))

export type SpecificShape = Record<Suit, number>
export const makeSpecificShape = (s: number, h: number, d: number, c: number) : SpecificShape => ({
  S: s,
  H: h,
  D: d,
  C: c
})
export const zeroSpecificShape = makeSpecificShape(0, 0, 0, 0)

export const groupHandBySuit = (hand: Hand) =>
  pipe(hand,
    RS.toReadonlyArray(ordCardDescending),
    RNEA.fromReadonlyArray,
    option.fold(() => readonlyRecord.empty, flow(
      RNEA.groupBy(c => c.suit),
      readonlyRecord.mapWithIndex((s: Suit, cards: RNEA.ReadonlyNonEmptyArray<Card>) => cards))))

export const getHandSpecificShape = (hand: Hand) : SpecificShape =>
  pipe(hand,
    groupHandBySuit,
    readonlyRecord.map(x => x.length),
    readonlyRecord.union(semigroup.first<number>())(zeroSpecificShape),
    (suits: readonlyRecord.ReadonlyRecord<Suit, number>) => suits)

export const getHandShape = (hand: Hand) : Shape =>
  pipe(hand,
    getHandSpecificShape,
    readonlyRecord.toReadonlyArray,
    RA.map(RT.snd),
    suitCounts => RA.mapWithIndex((idx, _) =>
      pipe(suitCounts, RA.lookup(idx), option.getOrElse(() => 0)))(zeroShape)) as Shape
